@import './variables.scss';
@import './styleguides.scss';

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 450;
  font-size: 16px;
}

::-moz-selection { /* Code for Firefox */
  color: $white;
  background: #39af38;
}

::selection {
  color: $white;
  background: #39af38;
}

body::-webkit-scrollbar {
  width: 0.8em;
  display: none;
}
 
body::-webkit-scrollbar-track {
  box-shadow: none;
  color: $white;
}
 
body::-webkit-scrollbar-thumb {
  background: #39af38;
  border-radius: 30px;
  clip-path: polygon(5% 20%, 95% 5%, 92% 98%, 8% 90%);
  background-clip: current-box;
}

a{
  color: $primary;
}

h1 {
  font-size: 1.8em;
  line-height: 38px;
}

.custom-control{
  border-radius: 8px;
  position: relative;
}

.custom-label {
  position: relative;
  box-sizing: border-box;
  background: $black; 
  border: 2px solid #1F1F1F;
  color: white;
  border-radius: 8px;
  padding: 1em;
  font-size: 1em;
  font-weight: 600;
  line-height: 100%;
  transition: all ease-out .4s;
  outline: 0 !important;

  &:-webkit-autofill,
  &:-webkit-autofill:hover, 
  &:-webkit-autofill:focus {
    -webkit-text-fill-color: white;
    -webkit-box-shadow: 0 0 0px 1000px #1F1F1F inset;
    transition: background-color 5000s ease-in-out 0s;
  }
}

.input-error{
  text-align: left;
  color: $alert;
  font-size: 0.8em !important;
  line-height: 150%;
}

.custom-label-error{
  border: 2px solid rgba(255, 94, 0, 0.7) !important;
  color: $alert !important;   
  height: inherit;
}

.custom-label:hover {
  background: #2F2F2F;
  border: 2px solid #2F2F2F;
}

.custom-label-text-error{
  color: $alert !important;   
}

.custom-label-checkbox-error{
  background: $alert !important;   
}

.custom-label-error ~ .dropdown-label,
.custom-label-error ~ .input-label{
  color: $black;
  background-color: $alert;
  padding: 0px 12px;
  left: 0px;
}
.custom-label-error .dropdown-label{
  color: $black;
  background-color: $alert;
  padding: 0px 12px;
  left: 0px;
}

.dropdown-label,
.input-label{
  position: absolute;
  top: -10px;
  left: 12px;
  font-size: 0.9em;
  z-index: 1;
  font-weight: 600;
  border-radius: 8px 8px 8px 0px;
  text-align: center;
  color: $lt-gray;
}


.custom-label:active,
.custom-label:focus,
.custom-label:focus-visible,
.custom-label:focus-within {
  border: 2px solid #2F2F2F;
}

.custom-checkbox-container {
  display: block;
  position: relative;
  padding-left: 35px;
  // margin-bottom: 12px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default checkbox */
.custom-checkbox-container input {
  position: absolute;
  opacity: 0;
  left: 0;
  cursor: pointer;
  height: 100%;
  width: 100%;
}

/* Create a custom checkbox */
.custom-checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  border-radius: 8px;
  background-color: #eee;
  cursor: pointer;
}

/* On mouse-over, add a grey background color */
.custom-checkbox-container:hover input ~ .custom-checkmark {
  background-color: rgb(170, 228, 155);
}

/* When the checkbox is checked, add a blue background */
.custom-checkbox-container input:checked ~ .custom-checkmark {
  background: $primary-gradient-darker;
}

/* Create the custom-checkmark/indicator (hidden when not checked) */
.custom-checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the custom-checkmark when checked */
.custom-checkbox-container input:checked ~ .custom-checkmark:after {
  display: block;
}

/* Style the custom-checkmark/indicator */
.custom-checkbox-container .custom-checkmark:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: solid $white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.tooltip {
  position: relative;
}

.tooltip .tooltip-text {
  visibility: hidden;
  white-space: nowrap;
  padding: 0.8em 1em;
  font-size: 0.8em;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 8px;
  transform: translate(-50%, -100%);
  left: 50%;
  top: -10px;
  opacity: 1;
  position: absolute;
  z-index: 2;
}

.tooltip:hover .tooltip-text {
  visibility: visible;
}

.darker-img-on-hover{
