@keyframes moving-green-shadow {
  0%{
    left: -3%;  
    width: 25%;
    transform: translate3d(0px, 0px, 0) scale(0.95);
  }
  17%{
    width: 50%;
    transform: translate3d(0px, 0px, 0) scale(0.7);
  }
  50%{
    width: 25%;
    left: 78%;
    transform: translate3d(0px, 0px, 0) scale(0.95);
  }
  83%{
    width: 50%;
    transform: translate3d(0px, 0px, 0) scale(0.7);
  }
  100%{
    left: -3%;
    width: 25%;
    transform: translate3d(0px, 0px, 0) scale(0.95);
  }
}

