  &.active{
    border: 2px solid rgba($primary, 0.4) !important;
  }

  
  &::-webkit-scrollbar {
    width: 0.5em;
    height: 0.5em;
    display: none;
  }
   
  &::-webkit-scrollbar-track {
    box-shadow: none;
    color: $white;
  }
   
  &::-webkit-scrollbar-thumb {
    background: #39af38;
    border-radius: 30px;
    clip-path: polygon(5% 20%, 95% 5%, 92% 98%, 8% 90%);
    background-clip: current-box;
  }
  
}

b{
  font-weight: 700 !important;
}